import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController, ToastController } from '@ionic/angular';
import { CommonService } from 'src/app/common.service';

@Component({
  selector: 'app-booking-modal',
  templateUrl: './booking-modal.page.html',
  styleUrls: ['./booking-modal.page.scss'],
})
export class BookingModalPage implements OnInit {
  @Input() date : any;
  @Input() cashInFlowDatas: any;
  bookingForm : FormGroup
  isProgress: Boolean;
  error = {
    customerName: '',
    mobile: '',
    address: '',
    requirement: ''
  }
  private colors = [
    'primary',
    'secondary',
    'tertiary',
    'success',
    'warning',
    'danger',
    'light',
    'medium',
    'dark',
  ];
  finalArray = [];
  constructor(
    private formBuilder: FormBuilder,
    private toastr: ToastController,
    private commonService: CommonService,
    private modal: ModalController
  ) { }


  getRandomColor(): string {
    const randomIndex = Math.floor(Math.random() * this.colors.length);
    return this.colors[randomIndex];
  }
  ngOnInit() {
    this.bookingForm = this.formBuilder.group({
      description: ['', [Validators.required]],
      category: ['', [Validators.required]],
      credit: [],
      debit: [],
      date: [this.date, [Validators.required]]
    })
    console.log(this.cashInFlowDatas);
    
    for(let index=0; index < this.cashInFlowDatas.length; index++) {
      if(new Date(this.cashInFlowDatas[index].date).toISOString() === new Date(this.date).toISOString()) {
        this.finalArray.push(this.cashInFlowDatas[index])
      }
    }
    console.log(this.finalArray);
    
  }

  submit() {
    this.isProgress = true;
    const payload = this.bookingForm.getRawValue();
    this.commonService.addPublicBooking(payload).subscribe((resp: any) => {
        this.toast('Your Booking Request has been submitted, Our Manager Will Contact You Soon...', 'secondary');
        this.isProgress = false;
        this.modal.dismiss();
    }, (err) => {
      if(err && err.error && err.error.status === 'Validation Failed') {
        this.error = err && err.error;
        console.log(this.error);
        this.isProgress = false;
      }
      this.isProgress = false;
    })
  }


  async toast(msg, color) {
    const toast = await this.toastr.create({
      position: 'bottom',
      message: msg,
      duration: 3000,
      color: color
    });
    await toast.present();  
  }
}
